import React, { Component } from "react";
import { app, base, auth, functions } from "./base";
import EditLink from "./EditLink";
import AddLink from "./AddLink";
import Links from "./Links";
import SearchSomething from "./SearchSomething";
import DeleteLink from "./DeleteLink";
import BestOfYouTube from "./BestOfYouTube";
import Modal from "./Modal";
import NewsFeed from "./NewsFeed";
import Weather from "./Weather";
import "./animate.css";
import Auth from "./Auth";

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      showModal: false,
      links: {},
      searchWhat: "",
      showModal: false,
      modalContent: 0,
      showEditPanel: false
    };
    this.addLink = this.addLink.bind(this);
    this.updateLink = this.updateLink.bind(this);
    this.filterLinks = this.filterLinks.bind(this);
    this.deleteLink = this.deleteLink.bind(this);
    this.openModal = this.openModal.bind(this);
    this.closeModal = this.closeModal.bind(this);
    this.keyDown = this.keyDown.bind(this);
    this.toggleEditPanel = this.toggleEditPanel.bind(this);
  }

  componentWillMount() {
    this.linksRef = base.syncState("links", {
      context: this,
      state: "links"
    });
  }

  componentWillUnmount() {
    base.removeBinding(this.linksRef);
  }

  addLink() {
    const links = { ...this.state.links };
    const id =
      this.state.links.length === undefined ? 0 : this.state.links.length;
    links[id] = {
      id: id,
      name: document.querySelector("input[name='name']").value,
      url: document.querySelector("input[name='url']").value
    };
    this.setState({ links });
    document.querySelector("input[name='url']").value = "";
    document.querySelector("input[name='name']").value = "";
  }
  updateLink() {
    const links = { ...this.state.links };
    const editThis = document.querySelector("select[name='which-id']").value;
    const nameEdit = document.querySelector("input[name='edit-name']").value;
    const urlEdit = document.querySelector("input[name='edit-url']").value;
    //console.log("this state: " + JSON.stringify(this.state.links));
    let update = { id: editThis, name: nameEdit, url: urlEdit };
    links[update.id] = update;

    this.setState({ links });
    document.querySelector("input[name='edit-url']").value = "";
    document.querySelector("input[name='edit-name']").value = "";
  }

  deleteLink() {
    const deleteThis = document.getElementById("deleteLink").value;
    const links = this.state.links.filter(function(link) {
      return link.name != deleteThis;
    });
    this.setState({ links });
    console.log("new links object: " + JSON.stringify(links));
  }

  filterLinks() {
    this.setState(previousState => {
      previousState.searchWhat = document
        .getElementById("filterLinks")
        .value.toLowerCase();
    });
  }

  openModal(whichModal) {
    this.setState({
      showModal: true,
      modalContent: whichModal
    });
  }

  closeModal() {
    this.setState({
      showModal: false
    });
  }

  keyDown(e) {
    if (e.keyCode === 27) {
      this.setState({
        showModal: false
      });
    }
  }

  toggleEditPanel() {
    if (this.state.showEditPanel === false) {
      this.setState({
        showEditPanel: true
      });
      setTimeout(function() {
        window.scrollTo(0, 2000);
      }, 200);
    } else {
      this.setState({
        showEditPanel: false
      });
    }
  }

  render() {
    const links = this.state.links;

    return (
      <div className="container" onKeyDown={this.keyDown}>
        <div className="row">
          <div className="col-md-12">
            <Weather />
          </div>
        </div>
        <div className="row well">
          <div className="col-md-4">
            <SearchSomething
              searchVal="https://www.google.com/#q="
              site="Google"
            />
          </div>
          <div className="col-md-4">
            <button
              type="button"
              className="btn btn-secondary btn-block"
              onClick={() => this.openModal(1)}
            >
              Calendar
            </button>
          </div>
          <div className="col-md-4">
            <SearchSomething
              searchVal="https://www.youtube.com/results?search_query="
              site="YouTube"
            />
          </div>
        </div>
        <div className="row">
          <div className="col-md-4 well">
            <Links
              links={this.state.links}
              start={0}
              end={16}
              searchWhat={this.state.searchWhat}
            />
          </div>
          <div className="col-md-4">
            <input
              type="text"
              id="filterLinks"
              className="form-control"
              placeholder="Search Links"
              onKeyUp={this.filterLinks}
            />
            <div>
              <BestOfYouTube />
            </div>
            <NewsFeed />
          </div>
          <div className="col-md-4 well">
            <Links
              links={this.state.links}
              start={17}
              end={33}
              searchWhat={this.state.searchWhat}
            />
          </div>
        </div>
        <div className="row well">
          <div className="col-md-4">
            <SearchSomething
              searchVal="https://www.rottentomatoes.com/search/?search="
              site="Rotten Tomatoes"
            />
          </div>
          <div className="col-md-4">
            {" "}
            <button
              onClick={this.toggleEditPanel}
              className="btn btn-block btn-secondary"
              id="toggledEdit"
            >
              Edit
            </button>
          </div>
          <div className="col-md-4">
            <SearchSomething
              searchVal="https://en.wikipedia.org/wiki/Special:Search/"
              site="Wikipedia"
            />
          </div>
        </div>
        <Auth toggleEditPanel={this.toggleEditPanel} />
        <div className={this.state.showEditPanel ? "row" : "row hide"}>
          <div className="col-md-4">
            <AddLink links={this.state.links} addLink={this.addLink} />
          </div>
          <div className="col-md-4">
            <EditLink links={this.state.links} updateLink={this.updateLink} />
          </div>
          <div className="col-md-4">
            <DeleteLink links={this.state.links} deleteLink={this.deleteLink} />
          </div>
        </div>
        <Modal
          modalContent={this.state.modalContent}
          showModal={this.state.showModal}
          closeModal={this.closeModal}
        />
        <div
          onClick={this.closeModal}
          className="modal-backdrop show"
          style={
            this.state.showModal ? { display: "block" } : { display: "none" }
          }
        />
      </div>
    );
  }
}

export default App;
